<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-alert
        title=""
        type="warning"
        center
        show-icon
        :closable="false"
      >
      </el-alert>

      <!--  -->
      <el-steps
        :active="nowStep - 0"
        finish-status="success"
        simple
        style="margin-top: 20px"
        process-status="finish"
      >
        <el-step title="" icon="el-icon-edit"></el-step>
        <el-step title="" icon="el-icon-menu"></el-step>
        <el-step title="" icon="el-icon-menu"></el-step>
        <el-step title="" icon="el-icon-picture"></el-step>
        <el-step title="" icon="el-icon-document"></el-step>
      </el-steps>

      <!-- tab -->
      <el-form
        ref="addGoodsForm"
        :model="addGoodsForm"
        label-width="auto"
        :rules="addGoodsFormRules"
        label-position="top"
      >
        <!-- tab -->
        <el-tabs
          tab-position="left"
          v-model="nowStep"
          :before-leave="handleTabChange"
          @tab-click="getAttrListById"
        >
          <!--  -->
          <el-tab-pane name="0">
            <span slot="label"><i class="el-icon-edit"></i> </span>
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addGoodsForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model.number="addGoodsForm.goods_price"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model.number="addGoodsForm.goods_number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model.number="addGoodsForm.goods_weight"></el-input>
            </el-form-item>
            <el-form-item label="Tips">
              <!--  -->
              <el-cascader
                v-model="addGoodsForm.goods_cat"
                :options="goodsCateList"
                :props="cateProps"
                @change="handleChange"
                clearable
                placeholder=""
                style="width: 25%"
              ></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="1">
            <span slot="label"><i class="el-icon-menu"></i> </span>
            <el-form-item
              v-for="(attr, index) in manyAttrList"
              :label="attr.attr_name"
              :key="index"
            >
              <el-checkbox-group
                v-model="attr.manySelectedAttr"
                @change="handleCahange"
              >
                <el-checkbox
                  v-for="(attr1, index1) in attr.attr_vals"
                  :label="attr1"
                  :key="index1"
                  checked
                  >{{ attr1 }}</el-checkbox
                >
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="2">
            <span slot="label"><i class="el-icon-menu"></i> </span>
            <el-form-item
              v-for="(attr, index) in onlyAttrList"
              :label="attr.attr_name"
              :key="index"
            >
              <el-checkbox-group
                v-model="attr.onlySelectedAttr"
                @change="handleCahange"
              >
                <el-checkbox
                  v-for="(attr1, index1) in attr.attr_vals"
                  :label="attr1"
                  :key="index1"
                  checked
                  >{{ attr1 }}</el-checkbox
                >
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane name="3">
            <span slot="label"><i class="el-icon-picture"></i> </span>
            <!--  -->
            <el-upload
              class="upload-demo"
              :action="actionUrl"
              :headers="headers"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
              drag
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em></em>
              </div>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload>
            <!--  -->
            <el-dialog
              title=""
              :visible.sync="previewImgDialog"
              width="60%"
              @close="previewImgDialog = false"
            >
              <img
                :src="this.previewImgUrl"
                alt=""
                style="width: 100%"
              />
            </el-dialog>
          </el-tab-pane>
          <el-tab-pane label="" name="4">
            <span slot="label"><i class="el-icon-document"></i> </span>
            <quill-editor
              v-model="addGoodsForm.goods_introduce"
            />
            <el-button type="success" class="add-btn" @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>
  </div>
</template>
<script>

// lodash
import _ from 'lodash'

export default {
  data() {
    return {
      // tab
      nowStep: '0',
      // 
      addGoodsForm: {
        goods_name: '',
        goods_cat: [],
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 
      addGoodsFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', message: '', trigger: 'blur' }
        ]
      },
      // 
      goodsCateList: [],
      // 
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      manyAttrList: [],
      // 
      onlyAttrList: [],
      // 
      actionUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      // token
      headers: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewImgUrl: '',
      // 
      previewImgDialog: false
    }
  },
  created() {
    // 
    this.getGoodsCateList()
  },
  methods: {
    // 
    async getGoodsCateList() {
      // 
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.goodsCateList = res.data
    },
    // 
    handleChange() {
      // 
      // 
      if (this.addGoodsForm.goods_cat.length !== 3) {
        this.addGoodsForm.goods_cat = []
        return null
      }
      console.log(this.addGoodsForm.goods_cat)
    },
    // tab
    handleTabChange(activeName, oldActiveName) {
      // tabtabname
      if (oldActiveName === '0') {
        // 
        // 
        if (this.addGoodsForm.goods_cat.length !== 3) {
          this.$message.error('')
          return false
        } else if (this.addGoodsForm.goods_name.trim() === '') {
          this.$message.error('')
          return false
        } else if (this.addGoodsForm.goods_price < 0) {
          this.$message.error('')
          return false
        } else if (this.addGoodsForm.goods_weight < 0) {
          this.$message.error('')
          return false
        } else if (this.addGoodsForm.goods_number < 0) {
          this.$message.error('')
          return false
        }
      }
    },
    // 
    async getAttrListById() {
      //  name1
      if (this.nowStep === '1' && this.manyAttrList.length === 0) {
        // 
        const { data: res } = await this.$http.get(
          `categories/${this.getCateId}/attributes`,
          {
            params: { sel: 'many' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // attr_vals
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          // 
          item.manySelectedAttr = []
        })
        this.manyAttrList = res.data
      } else if (this.nowStep === '2' && this.onlyAttrList.length === 0) {
        // 
        const { data: res } = await this.$http.get(
          `categories/${this.getCateId}/attributes`,
          {
            params: { sel: 'only' }
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // attr_vals
        res.data.forEach((item) => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          // 
          item.onlySelectedAttr = []
        })
        this.onlyAttrList = res.data
      }
    },
    // 
    handleCahange() {
      // console.log(this.manySelectedAttr)
      // console.log(this.onlyAttrList)
    },
    // 
    handlePreview(file) {
      // url
      this.previewImgUrl = file.response.data.url
      // 
      this.previewImgDialog = true
    },
    // 
    handleRemove(file) {
      // 
      // 
      // file.response.data.tmp_path
      // 
      const removeImg = file.response.data.tmp_path
      // 
      const removeIndex = this.addGoodsForm.pics.findIndex(
        (item) => item.pic === removeImg
      )
      // 
      this.addGoodsForm.pics.splice(removeIndex, 1)
    },
    // 
    handleSuccess(response) {
      // pics
      this.addGoodsForm.pics.push({ pic: response.data.tmp_path })
    },
    // 
    addGoods() {
      // 
      this.$refs.addGoodsForm.validate(async vaild => {
        if (!vaild) {
          return null
        }
        // 
        const newAddGoodsParams = _.cloneDeep(this.addGoodsForm)
        // goods_cat
        newAddGoodsParams.goods_cat = newAddGoodsParams.goods_cat.join(',')
        // atrrs
        // 
        this.manyAttrList.forEach(item => {
          newAddGoodsParams.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.manySelectedAttr.join(' ')
          })
        })
        // 
        this.onlyAttrList.forEach(item => {
          newAddGoodsParams.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.onlySelectedAttr.join(' ')
          })
        })
        // 
        const { data: res } = await this.$http.post('goods', newAddGoodsParams)
        if (res.meta.status !== 201) return this.$message.error('')
        // 
        this.$message.success('')
        // 
        this.$router.push('/goods')
      })
    }
  },
  computed: {
    // id
    getCateId() {
      // 
      // 
      if (this.addGoodsForm.goods_cat.length !== 3) return null
      // 
      //   id
      return this.addGoodsForm.goods_cat[this.addGoodsForm.goods_cat.length - 1]
    }
  }
}
</script>

<style lang="less" scoped>
// 
/deep/.el-upload-list__item {
  float: left;
  width: 30%;
  margin-right: 15px;
}

// 
/deep/.ql-container {
  min-height: 350px;
}

// 
.add-btn {
  margin-top: 15px;
}
</style>
