<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <el-card class="box-card">
      <!--   -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getGoodsList"
            @blur="searchGoodsList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchGoodsList"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button
            type="success"
            icon="el-icon-circle-plus-outline"
            @click="goTOAddGoods"
            ></el-button
          >
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodsList" stripe style="width: 100%" border>
        <!--  -->
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="goods_name" label="">
        </el-table-column>
        <el-table-column prop="goods_price" label="" width="95px">
        </el-table-column>
        <el-table-column prop="goods_number" label="" width="95px">
        </el-table-column>
        <el-table-column prop="goods_weight" label="" width="95px">
        </el-table-column>
        <el-table-column label="" width="180px">
            <template slot-scope="scope">
                {{scope.row.add_time | cqDateFormat}}
            </template>
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
            ></el-button>
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              @click="deleteGoods(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      total: 0,
      // 
      goodsList: [],
      // 
      isSearch: false
    }
  },
  created() {
    //   
    this.getGoodsList()
  },
  methods: {
    async getGoodsList() {
      //  
      // 1
      if (this.isSearch) {
        this.queryInfo.pagenum = 1
        // 
        this.isSearch = false
      }
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('')
      // 
      this.goodsList = res.data.goods
      // 
      this.total = res.data.total
    },
    // 
    searchGoodsList() {
      // 
      this.isSearch = true
      this.getGoodsList()
    },
    // 
    handleSizeChange(pageSize) {
      // 
      this.queryInfo.pagesize = pageSize
      // 
      this.getGoodsList()
    },
    // 
    handleCurrentChange(pageNum) {
      // 
      this.queryInfo.pagenum = pageNum
      // 
      this.getGoodsList()
    },
    // 
    async deleteGoods(row) {
      // 
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result === 'cancel') {
        return null
      }
      const { data: res } = await this.$http.delete('goods/' + row.goods_id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success('')
      // 
      this.getGoodsList()
    },
    // 
    goTOAddGoods() {
      this.$router.push('/goods/add')
    }
  }
}
</script>

<style lang="less" scoped>
</style>
