<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <!--  -->
      <el-alert
        title=""
        type="warning"
        show-icon
      >
      </el-alert>

      <!--  -->
      <el-row class="select-goods">
        <el-col>
          <span></span>
          <!--  -->
          <!--  -->
          <el-cascader
            v-model="selectedCate"
            :options="goodsCateList"
            :props="cateProps"
            @change="handleChange"
            clearable
            placeholder=""
            style="width: 25%"
          ></el-cascader>
        </el-col>
      </el-row>

      <!-- tab -->
      <el-row class="select-goods">
        <el-col>
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="" name="many">
              <!--  -->
              <el-button
                type="success"
                icon="el-icon-circle-plus-outline"
                size="mini"
                :disabled="isBtnDisabled"
                @click="showAddParamsDialog"
                >{{ getActiveTabName }}</el-button
              >
              <!-- table -->
              <el-table :data="manyAttrList" stripe style="width: 100%" border>
                <!--  -->
                <el-table-column type="expand">
                  <template slot-scope="scope">
                    <el-tag
                    :key="tag"
                    v-for="(tag, i) in scope.row.attr_vals"
                    closable
                    @close="handleClose(i, scope.row)"
                  >
                    {{ tag }}
                  </el-tag>
                  <el-input
                    class="input-new-tag"
                    v-if="scope.row.inputVisible"
                    v-model="scope.row.inputValue"
                    ref="saveTagInput"
                    size="small"
                    @keyup.enter.native="handleInputConfirm(scope.row)"
                    @blur="handleInputConfirm(scope.row)"
                  >
                  </el-input>
                  <el-button
                    v-else
                    class="button-new-tag"
                    size="small"
                    @click="showInput(scope.row)"
                    >+ New Tag</el-button
                  >
                  </template>
                </el-table-column>
                <!--  -->
                <el-table-column type="index" label="#"> </el-table-column>
                <el-table-column prop="attr_name" label="">
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                      @click="showEditParamsDialog(scope.row)"
                      ></el-button
                    >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deleteParams(scope.row)"
                      ></el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
            <el-tab-pane label="" name="only">
              <!--  -->
              <el-button
                type="success"
                icon="el-icon-circle-plus-outline"
                size="mini"
                :disabled="isBtnDisabled"
                @click="showAddParamsDialog"
                >{{ getActiveTabName }}</el-button
              >
              <!-- table -->
              <el-table :data="onlyAttrList" stripe style="width: 100%" border>
                <!--  -->
                <el-table-column type="expand"> </el-table-column>
                <!--  -->
                <el-table-column type="index" label="#"> </el-table-column>
                <el-table-column prop="attr_name" label="">
                </el-table-column>
                <el-table-column label="">
                  <template slot-scope="scope">
                    <el-button
                      type="primary"
                      icon="el-icon-edit"
                      size="mini"
                      @click="showEditParamsDialog(scope.row)"
                      ></el-button
                    >
                    <el-button
                      type="danger"
                      icon="el-icon-delete"
                      size="mini"
                      @click="deleteParams(scope.row)"
                      ></el-button
                    >
                  </template>
                </el-table-column>
              </el-table>
            </el-tab-pane>
          </el-tabs>
        </el-col>
      </el-row>
    </el-card>

    <!--  -->
    <el-dialog
      :title="'' + getActiveTabName"
      :visible.sync="addParamsDialog"
      width="30%"
      @close="resetAddParams"
    >
      <el-form
        ref="addParamsForm"
        :model="addParamsForm"
        label-width="100px"
        :rules="addParamsFormRules"
      >
        <el-form-item :label="getActiveTabName + ''" prop="attr_name">
          <el-input v-model="addParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetAddParams"> </el-button>
        <el-button type="primary" @click="addParams"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      :title="'' + getActiveTabName"
      :visible.sync="editParamsDialog"
      width="30%"
      @close="resetEditParams"
    >
      <el-form
        ref="editParamsForm"
        :model="editParamsForm"
        label-width="100px"
        :rules="editParamsFormRules"
      >
        <el-form-item :label="getActiveTabName + ''" prop="attr_name">
          <el-input v-model="editParamsForm.attr_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetEditParams"> </el-button>
        <el-button type="primary" @click="editParams"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      goodsCateList: [],
      // 
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      selectedCate: [],
      // tab
      activeName: 'many',
      // 
      manyAttrList: [],
      // 
      onlyAttrList: [],
      // 
      addParamsDialog: false,
      // 
      addParamsForm: {
        attr_name: '',
        attr_sel: 'many'
      },
      // 
      addParamsFormRules: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      editParamsDialog: false,
      // 
      editParamsForm: {
        attr_name: ''
      },
      // 
      editParamsFormRules: {
        attr_name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    // 
    this.getGoodsCateList()
  },
  methods: {
    // 
    async getGoodsCateList() {
      // 
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.goodsCateList = res.data
    },
    // 
    async getAttrList() {
      // 
      // 
      if (this.selectedCate.length !== 3) {
        this.selectedCate = []
        // 
        this.onlyAttrList = []
        this.manyAttrList = []
        return null
      }
      // 
      const { data: res } = await this.$http.get(
        `categories/${this.getCateId}/attributes`,
        {
          params: { sel: this.activeName }
        }
      )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // attr_vals
      res.data.forEach((item) => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        // 
        item.inputVisible = false
        // 
        item.inputValue = ''
      })
      console.log(res.data)
      // 
      if (this.activeName === 'many') {
        this.manyAttrList = res.data
      } else {
        this.onlyAttrList = res.data
      }
    },
    // 
    handleChange() {
      // 
      this.getAttrList()
    },
    // tab
    handleClick() {
      // 
      this.getAttrList()
      // 
      this.addParamsForm.attr_sel = this.activeName
    },
    // 
    showAddParamsDialog() {
      this.addParamsDialog = true
    },
    // 
    resetAddParams() {
      this.addParamsDialog = false
      // 
      this.$refs.addParamsForm.resetFields()
    },
    // 
    addParams() {
      // 
      this.$refs.addParamsForm.validate(async (vaild) => {
        //   
        if (!vaild) return null
        // 
        const { data: res } = await this.$http.post(
          `categories/${this.getCateId}/attributes`,
          this.addParamsForm
        )
        // 
        if (res.meta.status !== 201) { return this.$message.error('' + this.getActiveTabName + '') }
        // 
        this.addParamsDialog = false
        this.$message.success('' + this.getActiveTabName + '')
        // 
        // 
        this.getAttrList()
      })
    },
    // 
    async showEditParamsDialog(row) {
      // 
      // id
      const { data: res } = await this.$http.get(
        `categories/${this.getCateId}/attributes/${row.attr_id}`,
        {
          params: {
            attr_sel: this.activeName
          }
        }
      )
      if (res.meta.status !== 200) { return this.$message.error('id') }
      this.editParamsForm = res.data
      // 
      this.editParamsDialog = true
    },
    // 
    resetEditParams() {
      this.editParamsDialog = false
      // 
      this.$refs.editParamsForm.resetFields()
    },
    // 
    editParams() {
      // 
      this.$refs.editParamsForm.validate(async (vaild) => {
        //   
        if (!vaild) return null
        // 
        const { data: res } = await this.$http.put(
          `categories/${this.getCateId}/attributes/${this.editParamsForm.attr_id}`,
          {
            attr_name: this.editParamsForm.attr_name,
            attr_sel: this.editParamsForm.attr_sel
          }
        )
        // 
        if (res.meta.status !== 200) { return this.$message.error('' + this.getActiveTabName + '') }
        // 
        this.editParamsDialog = false
        this.$message.success('' + this.getActiveTabName + '')
        // 
        // 
        this.getAttrList()
      })
    },
    // 
    async deleteParams(row) {
      // 
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result === 'cancel') {
        return null
      }
      const { data: res } = await this.$http.delete(
        `categories/${this.getCateId}/attributes/${row.attr_id}`
      )
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success('')
      // 
      this.getAttrList()
    },
    // 
    async saveToData(row) {
      // 
      const { data: res } = await this.$http.put(`categories/${this.getCateId}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) { return this.$message.error('') }
      this.$message.success('')
    },
    // 
    handleInputConfirm (row) {
      //  
      row.inputVisible = false
      // 
      // 
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        return null
      }
      row.attr_vals.push(row.inputValue)
      // 
      row.inputValue = ''
      // 
      this.saveToData(row)
    },
    // 
    handleClose(i, row) {
      // 
      row.attr_vals.splice(i, 1)
      // 
      this.saveToData(row)
    },
    // 
    showInput(row) {
      // 
      row.inputVisible = true
      // $this.$nextTick
      this.$nextTick(_ => {
        // 
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  computed: {
    // 
    isBtnDisabled() {
      // 
      return this.selectedCate.length !== 3
    },
    // id
    getCateId() {
      // 
      // 
      if (this.selectedCate.length !== 3) return null
      // 
      //   id
      return this.selectedCate[this.selectedCate.length - 1]
    },
    // 
    getActiveTabName() {
      if (this.activeName === 'only') return ''
      else return ''
    }
  }
}
</script>

<style lang="less" scoped>
// 
.select-goods {
  margin: 15px 0;
}
// 
.el-tag {
  margin: 10px;
}
// 
.input-new-tag {
  width: 140px;
}
</style>
