<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col>
          <el-button
            type="success"
            icon="el-icon-circle-plus-outline"
            @click="showAddGoodsCateDialog"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        class="tree-table"
        :data="goodsCateList"
        :columns="goodsCateColumn"
        border
        :show-row-hover="false"
        :selection-type="false"
        show-index
        index-text="#"
        :expand-type="false"
      >
        <!--  -->
        <template slot="isOK" slot-scope="scope">
          <i
            class="el-icon-success"
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
          ></i>
          <i class="el-icon-error" v-else style="color: red"></i>
        </template>

        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            size="mini"
            type="success"
            v-else-if="scope.row.cat_level === 1"
            ></el-tag
          >
          <el-tag size="mini" type="warning" v-else></el-tag>
        </template>
        <!--  -->
        <template slot="opt" slot-scope="scope">
          <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditGoodsNameDialog(scope.row)"
            ></el-button
          >
          <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteGoodsCate(scope.row.cat_id)"
            ></el-button
          >
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 10, 15]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="resetAddGoosCate"
    >
      <el-form
        ref="addCateForm"
        :model="addCateForm"
        label-width="100px"
        :rules="addCateFormRules"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <!--  -->
          <el-cascader
            v-model="selectedCate"
            :options="goodsParentCateList"
            :props="cateProps"
            @change="handleChange"
            clearable
            style="width: 100%"
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetAddGoosCate"> </el-button>
        <el-button type="primary" @click="addGoodsCate"
          > </el-button
        >
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editGoodsNameDialogVisible"
      width="50%"
      @close="reseteditGoosName"
    >
      <el-form
        ref="editCateForm"
        :model="editCateForm"
        label-width="100px"
        :rules="editCateFormRules"
      >
        <el-form-item label="" prop="cat_name">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="reseteditGoosName"> </el-button>
        <el-button type="primary" @click="editGoodsCate"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        type: null,
        pagenum: 1,
        pagesize: 3
      },
      // 
      total: 0,
      // 
      goodsCateList: [],
      // 
      goodsCateColumn: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          prop: '',
          type: 'template',
          template: 'isOK'
        },
        {
          label: '',
          prop: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          prop: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 
      addCateDialogVisible: false,
      // 
      goodsParentCateList: [],
      // 
      addCateForm: {
        cat_pid: 0,
        cat_name: '',
        cat_level: 0
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // id
      selectedCate: [],
      // 
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: 'true'
      },
      // 
      editGoodsNameDialogVisible: false,
      // 
      editCateForm: {
        cat_name: ''
      },
      // 
      editCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    // 
    this.getGoodsCateList()
  },
  methods: {
    // 
    async getGoodsCateList() {
      // 
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // 
      this.goodsCateList = res.data.result
      // total
      this.total = res.data.total
    },
    // 
    async getParentCateList() {
      // 
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })
      if (res.meta.status !== 200) { return this.$message.error('') }
      // 
      this.goodsParentCateList = res.data
    },
    // 
    handleSizeChange(val) {
      // 
      this.queryInfo.pagesize = val
      // 
      this.getGoodsCateList()
    },
    // 
    handleCurrentChange(val) {
      // 
      this.queryInfo.pagenum = val
      // 
      this.getGoodsCateList()
    },
    // 
    showAddGoodsCateDialog() {
      // 
      this.getParentCateList()
      // 
      this.addCateDialogVisible = true
    },
    // 
    handleChange() {
      //  
      if (this.selectedCate.length === 0) {
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
        return null
      }
      // id
      this.addCateForm.cat_pid = this.selectedCate[this.selectedCate.length - 1]
      // 
      this.addCateForm.cat_level = this.selectedCate.length
    },
    // 
    resetAddGoosCate() {
      // 
      this.$refs.addCateForm.resetFields()
      // 
      this.selectedCate = []
      // 
      this.addCateForm.cat_pid = 0
      this.addCateForm.cat_level = 0
    },
    // 
    addGoodsCate() {
      // 
      this.$refs.addCateForm.validate(async vaild => {
        if (!vaild) return null
        // 
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        // 
        this.addCateDialogVisible = false
        // 
        this.getGoodsCateList()
      })
    },
    // id
    async getGoodsCateInfoById(id) {
      // 
      const { data: res } = await this.$http.get(`categories/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editCateForm = res.data
    },
    // 
    showEditGoodsNameDialog(goods) {
      // 
      this.getGoodsCateInfoById(goods.cat_id)
      this.editGoodsNameDialogVisible = true
    },
    // 
    reseteditGoosName() {
      this.editGoodsNameDialogVisible = false
      // 
      this.$refs.editCateForm.resetFields()
    },
    // 
    editGoodsCate() {
      // 
      this.$refs.editCateForm.validate(async vaild => {
        if (!vaild) return null
        const { data: res } = await this.$http.put(`categories/${this.editCateForm.cat_id}`, {
          cat_name: this.editCateForm.cat_name
        })
        if (res.meta.status !== 200) { return this.$message.error('') }
        this.$message.success('')
        // 
        this.editGoodsNameDialogVisible = false
        // 
        this.getGoodsCateList()
      })
    },
    // 
    async deleteGoodsCate(id) {
      // 
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result === 'cancel') {
        return null
      }
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success(res.meta.msg)
      // 
      this.getGoodsCateList()
    }
  }
}
</script>

<style lang="less" scoped>
// 
.tree-table {
  margin: 15px 0;
}
</style>
